.toggle {
  width: 36px;
  height: 24px;
  background: transparent;
  position: relative;
  padding: initial;
  cursor: pointer;
  border: unset;
  transition: all 0.35s ease-in-out;
  z-index: 4;
  outline: none;
  color: #ffffff;
  margin-right: 1.6rem;
}
@media (min-width: 1024px) {
  .toggle {
    margin-right: 0;
    display: none;
  }
}
.toggle:before, .toggle:after, .toggle__line {
  right: 0;
  position: absolute;
  width: 100%;
  height: 2px;
  background: #000000;
  transition: all 0.25s ease-in-out;
}
.toggle:before {
  top: 0;
}
.toggle:after {
  top: 100%;
  width: 100%;
}
.toggle__line {
  top: 54.5%;
  transform: translateY(-54.5%);
}
.toggle--open:before {
  transform: rotate(-45deg) translate(-6px, 6px);
}
.toggle--open .toggle__line {
  opacity: 0;
}
.toggle--open:after {
  transform: rotate(45deg) translate(-10px, -11px);
  width: 100%;
}

